html {
  font-family: "Lato", "Lucida Grande", "Lucida Sans Unicode", Tahoma,
    Sans-Serif;
  line-height: 1.5;
  font-size: 15px;
  font-weight: 400;
}

body {
  padding: 0;
  margin: 0;
  background: #fefefe;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1 {
  font-size: 1.2rem;
  text-align: center;
}

.l-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.l-board {
  padding: 16px;
}

.button {
  display: inline-block;
  border: 2px solid black;
  border-radius: 4px;
  transition: all 0.5 ease;
  font-weight: bold;
  width: 100%;
  text-align: center;
  cursor: pointer;
}

.button:hover {
  transition: all 0.5 ease;
  background: black;
  color: white;
}

.state-message {
  padding: 8px;
  text-align: center;
}

.cell.active {
  color: blue;
}

.turn {
  display: flex;
  padding: 8px 16px;
}

.turn-item {
  padding: 8px;
  font-size: 1.5rem;
  width: 100%;
  text-align: center;
}
